@charset "utf-8";
/*! 코딩 리스트에서 만 사용하는 css */
html{-webkit-text-size-adjust:100%;-ms-text-size-adjust:100%;line-height:1.15}
article,aside,audio,blockquote,body,canvas,code,dd,details,div,dl,dt,fieldset,figcaption,figure,footer,form,h1,h2,h3,h4,h5,h6,header,hgroup,input,legend,li,mark,nav,ol,p,pre,section,td,textarea,th,time,ul,video{margin:0;padding:0}
body{font-family:Malgun Gothic,"맑은 고딕",Gulim,"굴림","Helvetica Neue",Helvetica,Arial,Verdana,Tahoma,sans-serif;color:#000;background-color:#fff;font-size:12px}
img{vertical-align:middle;border:0}
button {padding: 0;}
button,input,optgroup,select,textarea{margin:0;font-size:100%;vertical-align:middle;font-family:sans-serif;line-height:1.15}
button,input{overflow:visible}
button,select{text-transform:none}
[type=reset],[type=submit],button,html [type=button]{-webkit-appearance:button}
[type=button]::-moz-focus-inner,[type=reset]::-moz-focus-inner,[type=submit]::-moz-focus-inner,button::-moz-focus-inner{border-style:none;padding:0}
[type=button]:-moz-focusring,[type=reset]:-moz-focusring,[type=submit]:-moz-focusring,button:-moz-focusring{outline:1px dotted ButtonText}
[type=checkbox],[type=radio]{-webkit-box-sizing:border-box;box-sizing:border-box;padding:0}
::-webkit-file-upload-button{-webkit-appearance:button;font:inherit}
table{border-collapse:collapse;border-spacing:0}
fieldset{border:0}
legend{color:#000}
address,caption,cite,code,dfn,em,strong,th,var{font-style:normal;font-weight:400}
ol,ul{list-style:none}
caption,th{text-align:left}
h1,h2,h3,h4,h5,h6{font-size:100%;font-weight:400;line-height:1}
caption,hr,legend{width:0;height:0;font-size:1px;text-indent:-9999em;line-height:0}
a,a:active,a:focus,a:hover,a:link,a:visited{color:#000;font-size:100%;text-decoration:none}
a{background-color:transparent;-webkit-text-decoration-skip:objects}
abbr[title]{border-bottom:none;text-decoration:underline;-webkit-text-decoration:underline dotted;-moz-text-decoration:underline dotted;text-decoration:underline dotted}
audio,video{display:inline-block}
audio:not([controls]){display:none;height:0}
svg:not(:root){overflow:hidden}


/*!
* 코딩리스트
* 공통 스타일
*/
.hide-txt{
    overflow:hidden;
    position:absolute;
    width:1px;height:1px;
    font-size:1px;line-height:0;
    clip: rect(0 0 0 0);
    white-space: nowrap;
    margin: -1px;padding: 0;border: 0;
}

/*!
 * 코딩리스트
 * 컨텐츠 스타일
 * 상단 안내 영역
 */
.cl-info-area{position:relative;width:100%;}
.cl-info-area .project-tit{font-size:32px;padding:20px 20px 20px 20px;border-bottom:2px solid #000}
.cl-link-area{
    width:100%;
    background:#fff;padding:0;border-bottom:2px solid #000;
    position:sticky;
    top:0;left:0;
    z-index:1;
}
.cl-link-area-sticky{position:fixed;}
.link-other{margin:0 0 0 10px;}
.link-other>a{display:inline-block;border:1px solid #000;margin-right:5px;padding:7px 20px 6px 20px;margin-top:15px;}
.link-other>a:hover{text-decoration: underline;}

.link-category-container{
    overflow:hidden;
    height:42px;
}
.link-category{
    text-align:left;padding-left:10px;
    overflow-y:hidden;
    overflow-x:auto;
    white-space: nowrap;
    height:60px;
}
.link-category>a{display:inline-block;border:1px solid #000;margin-right:5px;padding:6px 10px 4px 10px;margin-top:15px;}
.link-category>a:hover{text-decoration: underline;}

.page-count-area{margin:15px 0 15px 10px;}
.page-count-area span{display:inline-block;font-weight:700;margin-right:20px}
.page-count-area span span{font-weight:400;margin-left:5px;margin-right:0}

/*!
 * 코딩리스트
 * 컨텐츠 스타일
 * 화면 목록
 */
.cl-list-area{position:relative;width:100%;}
.cl-list-detail{width:100%;overflow-x:auto;}
.cl-category-tit{padding:20px 10px 10px 10px;font-weight:700}
.page-count-category{display:inline-block;}
.cl-table{white-space: nowrap;width:100%;}
th{padding:10px 5px;border:1px solid #e2e2e2;text-align:center;background:#cbf2f5}
td{padding:10px 5px;border:1px solid #e2e2e2;text-align:center}
tr:nth-child(even){background:#f1f9f9}
td:nth-child(1){white-space:normal;width:25px;min-width:25px;}
td:nth-child(2){white-space:normal;width:150px;min-width:150px;text-align:left}
td:nth-child(3){white-space:normal;width:150px;min-width:150px;text-align:left}
td:nth-child(4){white-space:normal;width:150px;min-width:150px;text-align:left}
td:nth-child(5){white-space:normal;width:150px;min-width:150px;text-align:left}
td:nth-child(6){white-space:normal;width:150px;min-width:150px;text-align:left}
td.cl-table-td-name{width:150px;min-width:150px;}
td.cl-table-td-path{width:150px;min-width:150px;text-align:left;}
td.cl-table-td-path .path-txt-wrap{position:relative;}
td.cl-table-td-path .path-txt-area{display:none;}
td.cl-table-td-path .path-btn-area{display:flex; align-items:center;}
tr:hover .path-txt-area {
    display: block;
    position: absolute;
    bottom: -8px; left: 105%;
    background: #eee;
    padding: 8px;
    border-radius: 5px;
}
td.cl-table-td-path a {
    color:#00f;
    text-decoration: underline;
    cursor: pointer;
}
td.cl-table-td-path a + a {
    margin-left: 8px;
}
td.cl-table-td-complete{width: 50px;}
td.cl-table-td-status{width:50px;}
td.cl-table-td-status[data-status="완료"]{color:green;}
td.cl-table-td-status[data-status="진행중"]{color:red;}
td.p-percent{width:50px}
td.p-script01{width:50px}
td.p-script02{width:50px}
td.p-etc{width:auto;text-align:left;}
td.p-etc div{margin-bottom:5px;}
td.p-etc .issue{color:red;}
td.p-etc .script{color:blue;}
td.p-etc .modify{color:#000000;font-weight: 700;}
.cl-category-link:last-child{margin-bottom:100px}

/*!
 * 코딩리스트
 * 유틸리티
 * 탑버튼
 */
a.btn-top{
    width:40px;
    height:40px;
    line-height:40px;
    text-align:center;
    border-radius:50%;
    position:fixed;
    bottom:30px;
    right:30px;
    background:#333638;
    color:#d9d9d9;
}
a.btn-top:hover{background: #53575c;}

/*!
 * 코딩리스트
 * 유틸리티
 * 모바일 프레임
 */
.frame-preview{
    position:fixed;
    right:-425px;
    top:50%;
    transform:translateY(-50%);
    z-index:100;
    transition: 0.2s;
}
.frame-preview .frame-wrap{background-color:#fff;border:15px solid #000;border-top-width:42px;border-radius:20px;}
.frame-preview .frame-head{position:absolute;top:3px;left:16px;right:16px;display:flex;justify-content:space-between;align-items:center;}
.frame-preview .frame-page{color:#fff;padding-right:10px;word-break:break-all;line-height:1.4;}
.frame-preview .frame-page:hover{text-decoration:underline}
.frame-preview .frame-sort select{color:#fff;background:none;border:none;outline:none;padding:0;}
.frame-preview .frame-sort select option{color:#000}
.frame-preview .frame-sort .frame-size{color:#fff;margin-top:1px;padding:0 3px;color:#a8a8a8}
.frame-preview .frame-inner iframe{overflow:hidden;width:100%;height:100%;}
.frame-preview .frame-toggle {
    background: #000;
    border: 0;
    position: absolute; left: -25px; top: 50%; transform: translateY(-50%);
    width: 25px; height: 50px;
    border-radius: 5px 0 0 5px;
    cursor: pointer;
    outline: none;
}
.frame-preview .frame-toggle:after {
    position: absolute;
    right: 50%;
    left: 50%;
    content:'';
    display: block;
    width: 5px;
    height: 5px;
    border-top: 2px solid #fff;
    border-right: 2px solid #fff;
    transform: translate(-30%, -50%) rotate(-135deg);
}
.frame-preview .frame-toggle.on:after {
    transform: translate(-70%, -50%) rotate(45deg);
}

/*!
 * 코딩리스트
 * 테마
 * 다크모드
 */
html{background-color:#1e1f21;}
body{background-color:#1e1f21;}
.cl-info-area{background-color:#1e1f21;}
.cl-info-area .project-tit{border-bottom:2px solid #3a3b3d;color:#d9d9d9;}
.cl-link-area{background:#292a2d;border-bottom:2px solid #3a3b3d;}
.link-other>a{border:1px solid #3a3b3d;color:#d9d9d9;}
.link-category>a{border:1px solid #3a3b3d;color:#d9d9d9;}
.page-count-area span{color:#a8a8a8;}
.cl-category-tit{color:#d9d9d9;background-color:#1e1f21;font-weight:400;font-size:16px;}
th{border:1px solid #3a3b3d;background:#292a2d;color:#d9d9d9;font-size:14px;}
td{border:1px solid #3a3b3d;color:#a8a8a8;background:#292a2d;}
tr:nth-child(even){background:#292a2d}
tr.cl-table-td-status[data-status="완료"]{color:#19ce60;}
tr.cl-table-td-status[data-status="진행중"]{color:#f05a5a;}
td:last-child .issue {color:#f05a5a}
td:last-child .script {color:#3565c9}
td:last-child .modify {color:#19ce60;font-weight: 400;}
td.cl-table-td-path a {color:#5bb8e1;}
tr:hover .path-txt-area{background:#000000;}

